import { Solution } from '.';

export class UI {
    solution: Solution;

    divTitle: HTMLDivElement;
    divSub: HTMLDivElement;
    divSubs: HTMLDivElement[] = [];
    
    divSubID: HTMLDivElement;
    divSubIDs: HTMLDivElement[] = [];
    constructor(solution: Solution) {
        this.solution = solution;
        this.initUI();
    }
    public initUI() {

        this.divTitle = document.createElement('div');
        this.solution.hostDiv.insertBefore(this.divTitle, this.solution.renderer.domElement);
        this.divTitle.textContent = 'Storymaps Metrics from Jan 24-2020';
        this.divTitle.style.color = '#fff';
        this.divTitle.style.fontSize = '64px';
        this.divTitle.style.position = 'absolute';
        
        this.divTitle.style.padding = '20px'
        this.divTitle.style.zIndex = '1';
        this.divTitle.style.paddingBottom = '10px';


        const div = document.createElement('div');
        this.solution.hostDiv.appendChild(div);
        div.style.position = 'absolute';
        div.style.padding = '20px';
        div.style.padding = '20px';
        div.style.marginTop = '100px';

        this.divTitle = document.createElement('div');
        div.appendChild(this.divTitle);
        this.divTitle.textContent = 'TSNE Visualization ';
        this.divTitle.style.fontSize = '20px';
        this.divTitle.style.pointerEvents = 'none';
        this.divTitle.style.paddingBottom = '10px';

        this.divTitle = document.createElement('div');
        div.appendChild(this.divTitle);
        this.divTitle.textContent = 'Keyword: ';
        this.divTitle.style.color = '#0ff';
        this.divTitle.style.fontSize = '38px';
        this.divTitle.style.pointerEvents = 'none';
        this.divTitle.style.paddingBottom = '10px';

        this.divSub = document.createElement('div');
        div.appendChild(this.divSub);
        this.divSub.textContent = 'Neighbors:';
        this.divSub.style.paddingTop = '5px';
        this.divSub.style.paddingBottom = '18px';

        for(let i = 0 ; i < 10; ++i) {
            const subs = document.createElement('div');
            subs.textContent = ''
            subs.style.pointerEvents = 'none';
            
            subs.style.paddingTop = '5px';
            subs.style.color = '#cc0';
            this.divSubs.push(subs);
            this.divSub.appendChild(subs);
        }



        this.divSubID = document.createElement('div');
        div.appendChild(this.divSubID);
        this.divSubID.textContent = 'Storymaps ID:';
        this.divSubID.style.paddingTop = '8px';
        this.divSubID.style.paddingBottom = '8px';

        for(let i = 0 ; i < 10; ++i) {
            const subs = document.createElement('div');
            subs.textContent = ''
              this.divSubIDs.push(subs);
            this.divSubID.appendChild(subs);
        }

    }
    public updateSub(data) {
        // console.log(data);
        let count = 0;
        for (let key in data) {

            this.divSubs[count++].textContent = key;
            // console.log(d, key);
            // this.mesh.geometry.attributes.color.setXYZ(d.id, 1, 0, 1);
        }
    }
    public updateSubID(data) {
        // console.log(data);
        try {
            let count = 0;
            for (let i = 0; i < data.length; ++i) {
                this.divSubIDs[count++].innerHTML = `<a href="https://storymaps.arcgis.com/stories/${data[i]}" style="font-size: 14px;" target="_blank" > ID: ${data[i]}</a>`
            }
        } catch (error) {

        }

    }
}